<template>
  <div class="whole">
    <el-container class="wholeBox">
        <el-header class="headerBox">
            <div class="hdB_left">
                <img :src="require('../../assets/img/loginZ.jpg')" alt="">
            </div>
            <div class="hdB_rigth">
                <el-dropdown @command="handleCommand">
                  <span class="el-dropdown-link">
                    {{buserInfo.username}}<i class="el-icon-arrow-down el-icon--right"></i>
                  </span>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command="e">退出</el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
            </div>
        </el-header>
        <!-- 这是大排行 -->
        <el-main class="Main">
          <!-- 这是导航 -->
            <el-menu
                  :default-active="defaultActive"
                  class="el-menu-vertical-demo"
                  @select="handleSelect">
                  <el-menu-item index="1">
                    <!-- 设备维修记录------在自功能内 -->
                    <span slot="title">客户管理</span>
                  </el-menu-item>
                  <!-- <el-menu-item index="2">
                    <span slot="title">创建工单</span>
                  </el-menu-item> -->
                  <el-menu-item index="3" :disabled="!buserInfo.is_superuser">
                    <span slot="title">员工管理</span>
                  </el-menu-item>
                  <!-- <el-menu-item index="4">
                    <span slot="title">客户历史投诉和解决方案</span>
                  </el-menu-item> -->
                  <el-menu-item index="5">
                    <span slot="title">更新知识库</span>
                  </el-menu-item>
                  <!-- 禁用disabled -->
                  <el-menu-item index="6" >
                    <span slot="title">更新行业资讯</span>
                  </el-menu-item>
                  <el-menu-item index="8" :disabled="ISdisabled">
                    <span slot="title">更新企业动态</span>
                  </el-menu-item>
                  <el-menu-item index="7">
                    <span slot="title">注册公司列表</span>
                  </el-menu-item>
            </el-menu>
            <div class="rigthBox">
                <router-view/>
            </div>
        </el-main>
    </el-container>
  </div>
</template>

<script>
import { mapState,mapActions } from "vuex";
export default {
    data(){
        return{
          defaultActive: sessionStorage.getItem('defaultActive')|| "1",
          ISdisabled:true
        }
    },
    methods:{
        ...mapActions(['DelABuserInfo']),
        handleCommand(command) {//这是登陆上面的处罚点击按钮
            // command--点击的子菜单的项目command的值
            if(command=='e'){
              this.DelABuserInfo()
              this.$router.push('/bklogin')
            }
            // this.$message('click on item ' + command);
        },
        handleSelect(key, keyPath) {//这是侧边栏的点击效果
            console.log(key, keyPath);
          sessionStorage.setItem("defaultActive",key);
          this.defaultActive=sessionStorage.getItem('defaultActive')
            switch (key) {
              case "1"://客户
                this.$router.push('/bkIndex/bkkeHu')
                break;
              case "2"://创建工单
                this.$router.push('/bkIndex/bkworkOrder')
                break;
              case "3"://用户管理
                this.$router.push('/bkIndex/bkuserManagement')
                break;
              case "4"://客户历史投诉和解决方案
                this.$router.push('/bkIndex/bkcomplaintAndSolve')
                break;
              case "5"://更新知识库
                this.$router.push('/bkIndex/bkknowledge')
                break;
              case "6"://更新行业资讯
                this.$router.push('/bkIndex/bknews')
                break;
              case "7"://注册公司列表
                this.$router.push('/bkIndex/companylist')
                break;
              case "8"://更新企业动态
                this.$router.push('/bkIndex/dynamicB')
                break;
            
              default:
                this.$router.push('/bkIndex/bkkeHu')
                break;
            }
        },
        Fndisabled(){//判断限权是否有什么
          if(this.buserInfo.u_type=='超级管理员'){
            this.ISdisabled=false
          }else if(this.buserInfo.u_type=='运维'){
            this.ISdisabled=false
          }else{
            this.ISdisabled=true
          }
        }
    },
    beforeRouteEnter(to,from,next){//和全局使用一样
      console.log(sessionStorage.getItem('bkL'),'------------hsxsx')
      let xx=sessionStorage.getItem('bkL');
      if(xx!='L成功'){
        next('/bklogin')
      }else{
        next()
        return
      }
    },
    computed:{
      ...mapState(['buserInfo'])
    },
    mounted(){
      this.Fndisabled()//调用权限判断
    }

}
</script>

<style lang="stylus" scoped>
.whole
    height 100vh
    .wholeBox
        height 100%
.headerBox
    height: 60px;
    line-height 60px;
    font-size: 12px;
    color: #fff;
    background-color: rgb(58, 63, 81);
    display: block;
    .hdB_left
        font-size 32px
        font-weight bold
        float left
        img 
          max-height 40px
          vertical-align middle
    .hdB_rigth
        float right
        .el-dropdown-link
            font-size 14px
            color #ffffff
.Main
    padding 0
    display flex
    position relative
    .el-menu-vertical-demo
        width 250px
        height 100%
        position fixed
        z-index 99
    .rigthBox
        flex 1
        margin-left 265px
</style>


